export function filter() {
    try {
        let getInputSearch = document.getElementById('filter');
        function conectToData(url) {
            fetch(url)
                .then(res => res.json())
                .then(data => {
                let getDataResults = data.results;
                let getValueOfInput = getInputSearch.value;
                let valuesToExpresion = new RegExp(getValueOfInput);
                let getFilterList = document.getElementById('filter_list');
                const filterDataName = () => {
                    let dataName = getDataResults.filter((item) => {
                        return (valuesToExpresion).test(item.name);
                    });
                    dataName.forEach((data) => {
                        let createPforList = document.createElement('p');
                        createPforList.textContent = data.name;
                        getFilterList.append(createPforList);
                    });
                };
                getValueOfInput.length > 2 ? filterDataName() : getValueOfInput = false;
            });
        }
        getInputSearch.addEventListener('keyup', () => {
            conectToData("https://rickandmortyapi.com/api/character");
        });
    }
    catch (error) { }
}
